.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 8.16px 8px 0 8px;
  padding: 0.51rem 0.5rem 0 0.5rem;
}
.container .search-zone {
  position: relative;
}
.container .search-zone input::-ms-input-placeholder {
  text-align: center;
}
.container .search-zone input::-webkit-input-placeholder {
  text-align: center;
}
.container .search-zone .search-input {
  border: 1px solid #333;
  width: 100%;
  height: 12.8px;
  height: 0.8rem;
  font-size: 4.8px;
  font-size: 0.3rem;
  padding-left: 1.6px;
  padding-left: 0.1rem;
}
.container .search-zone .search-icon {
  position: absolute;
  top: 0;
  right: 0;
  color: red;
}
.container .search-list {
  margin-top: 4.8px;
  margin-top: 0.3rem;
}
.container .search-list .score-item {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin-top: 1.6px;
  margin-top: 0.1rem;
  background: #e1e1e1;
  height: 100%;
  line-height: 9.6px;
  line-height: 0.6rem;
  font-size: 4.8px;
  font-size: 0.3rem;
  width: 100%;
  border-radius: 0.08rem;
}
.container .search-list .score-item .up {
  text-align: left;
}
.container .search-list .score-item .up .score-name {
  word-wrap: break-word;
  word-break: normal;
  text-align: left;
  padding-left: 3.2px;
  padding-left: 0.2rem;
  font-size: 3.84px;
  font-size: 0.24rem;
  font-weight: bold;
}
.container .search-list .score-item .down {
  position: relative;
  text-align: left;
  padding-left: 3.2px;
  padding-left: 0.2rem;
}
.container .search-list .score-item .down .score-store {
  position: absolute;
  right: 1.6px;
  right: 0.1rem;
  top: 0;
  width: 22.4px;
  width: 1.4rem;
  color: #fff;
  font-size: 3.84px;
  font-size: 0.24rem;
  background-color: #407696;
  border-radius: 0.5rem;
  height: 8px;
  height: 0.5rem;
  line-height: 8.8px;
  line-height: 0.55rem;
}
.container .search-list .score-item .down .score-store.active {
  background-color: grey;
}
.container .search-list .score-item .down .score-artist {
  margin-top: 0.48px;
  margin-top: 0.03rem;
  font-size: 3.2px;
  font-size: 0.2rem;
  font-weight: 100;
  color: #333;
  float: left;
  margin-right: 3.2px;
  margin-right: 0.2rem;
}
.container .search-list .score-item .down .score-level {
  font-size: 2.88px;
  font-size: 0.18rem;
  font-weight: 100;
  color: #407696;
  border: 1px solid #407696;
  padding: 0 1.6px;
  padding: 0 0.1rem;
  border-radius: 0.6rem;
}
