









































































































.container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0.51rem 0.5rem 0 0.5rem;
  .search-zone {
    position: relative;
    input::-ms-input-placeholder {
      text-align: center;
    }
    input::-webkit-input-placeholder {
      text-align: center;
    }
    .search-input {
      border: 1px solid #333;
      width: 100%;
      height: 0.8rem;
      font-size: 0.3rem;
      padding-left: 0.1rem;
    }
    .search-icon {
      position: absolute;
      top: 0;
      right: 0;
      color: red;
    }
  }
  .search-list {
    margin-top: 0.3rem;
    .score-item {
      display: inline-block;
      position: relative;
      overflow: hidden;
      margin-top: 0.1rem;
      background: #e1e1e1;
      height: 100%;
      line-height: 0.6rem;
      font-size: 0.3rem;
      width: 100%;
      border-radius: 0.08rem;
      .up {
        text-align: left;
        .score-name {
        word-wrap: break-word;
        word-break: normal;
        text-align: left;
        padding-left: 0.2rem;
        font-size: 0.24rem;
        font-weight: bold;
        }
      }
      .down {
        position: relative;
        text-align: left;
        padding-left: 0.2rem;
        .score-store {
          position: absolute;
          right: 0.1rem;
          top: 0;
          width: 1.4rem;
          color: #fff;
          font-size: 0.24rem;
          background-color: #407696;
          border-radius: 0.5rem;
          height: 0.5rem;
          line-height: 0.55rem;
          &.active {
            background-color: grey;
          }
        }
        .score-artist {
          margin-top: 0.03rem;
          font-size: 0.2rem;
          font-weight: 100;
          color: #333;
          float: left;
          margin-right: 0.2rem;
        }
        .score-level {
          font-size: 0.18rem;
          font-weight: 100;
          color: #407696;
          border: 1px solid #407696;
          padding: 0 0.1rem;
          border-radius: 0.6rem;
        }
      }
    }
  }
}
